body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #628469!important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

.App {
  margin: 0 auto;
  max-width: 1280px;
  min-width: 320px;
  font-family: Verdana;
  box-shadow: 4px 8px 20px 0px rgba(0, 0, 0, 0.75);
}

@media (max-width: 639px) {
  .App {
    position: fixed;
  }
}/*# sourceMappingURL=App.css.map */
.content {
  height: 78vh;
  display: grid;
  grid-template-columns: 2fr 1fr;
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  overflow: auto;
}
.content .list {
  cursor: pointer;
  background-color: #e94951;
  color: #fff;
  padding: 2rem 1rem;
  margin: 0.5rem;
  border-radius: 0.5rem;
}

.karte {
  background-color: #8ebc1e;
  grid-area: 1/1/2/2;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.5rem;
  /* On mouse-over, add a deeper shadow */
  /* Add some padding inside the ngcard container */
}
.karte .ngcard {
  /* Add shadows to create the "ngcard" effect */
  border-radius: 0.5rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  background-color: gainsboro;
}
.karte .ngcard:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}
.karte .container {
  color: #003f20;
  max-width: 80%;
  padding: 1rem 0;
}

.post-title {
  text-transform: capitalize;
  font-family: Verdana;
}

.provider {
  background-color: #e60a14;
  grid-area: 1/2/2/3;
  overflow: auto;
}

@media (max-width: 639px) {
  .content {
    height: 88vh;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 2fr 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
  }
  .content .list {
    font-size: 0.8rem;
    padding: 0.6rem;
  }
  .karte {
    grid-area: 1/1/2/2;
  }
  .provider {
    grid-area: 2/1/3/2;
  }
}/*# sourceMappingURL=Content.css.map */
.footer {
  background-color: #6e1e6e;
  position: -webkit-sticky;
  position: sticky;
  bottom: 0px;
  min-width: 100%;
  height: 12vh;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
}/*# sourceMappingURL=Footer.css.map */
.header {
  background-color: #fff000;
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
}

.navi {
  list-style: none inside;
  margin: 0;
  padding: 0;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  text-align: center;
}

.navi li {
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
  margin-right: 0.5rem;
  position: relative;
  background: #8ebc1e; /* pro zero light green */
  border-radius: 0.1rem;
}

.navi li a {
  display: block;
  padding: 0;
  text-decoration: none;
  width: 7rem;
  padding: 0.7rem;
  color: #ffffff;
}

.navi li li a {
  font-size: 0.8rem;
  background-color: #8ebc1e;
}

.navi li li {
  position: relative;
  left: 1rem;
  list-style: none;
  margin-top: 0.05rem;
}

.navi li:hover {
  background: #005151;
}

.navi ul {
  position: absolute;
  padding: 0;
  left: 0;
  display: none;
}

.navi li:hover ul {
  display: block;
}

.navi li li:hover ul {
  display: block;
}

.navi li li:hover a {
  background-color: #005151;
}

.ham {
  color: #005151;
  width: 2rem;
  height: 2rem;
  border: #005151 solid 1px;
  cursor: pointer;
  border-radius: 0.2rem;
  background-color: #ffffff;
  padding-left: 7px;
  opacity: 0.9;
  display: none;
  align-items: center;
  justify-content: center;
  position: fixed;
  z-index: 20;
  top: 1rem;
  right: 1rem;
}

@media (max-width: 639px) {
  .header {
    position: absolute;
    z-index: 10;
    right: 0vh;
    background-image: linear-gradient(to right top, #005151, #006e5c, #008b57, #4ca544, #8ebc1e);
    height: 100vh;
    width: 100vw;
    transition: 0.5s;
  }
  .header .navi {
    position: absolute;
    top: 4rem;
    left: 3rem;
    flex-direction: column;
  }
  .header .navi li {
    margin-bottom: 3rem;
  }
  .header .navi li li {
    position: relative;
    left: 7rem;
    bottom: 4rem;
    margin-bottom: 0.05rem;
  }
  .ham {
    display: block;
  }
  .header.active {
    right: -100vh;
    transition: 0.5s;
  }
}/*# sourceMappingURL=Navigation.css.map */
